'use strict';

import React from 'react';

require('./index.less');

class ArticleDetail extends React.Component {
    render() {
        return (
        	<div className="com-article-detail">
        		<div className="article-detail-hd imgcover">
        			<img src={'/images/articles/banner.jpg'} />
        		</div>
        		<div className="article-detail-bd">
        			<div className="detail">
						<p><a href=""></a> Pauline van Dongen </p>
						<p>MP3 GPS  USB </p>
						<p>van Dongen  2013 </p>
						<p></p>
						<p> <a href="http://www.dezeen.com/2016/08/15/pauline-van-dongen-solar-powered-windbreaker-jacket-charges-phone/" rel="nofollow">dezeen&nbsp;</a></p>
					</div>
        		</div>
        		<div className="article-detail-ft">
        			<ul className="tags">
        				<li className="tag"></li>
        				<li className="tag"></li>
        				<li className="tag"></li>
        			</ul>
        		</div>
        	</div>
        );
    }
}

ArticleDetail.displayName = 'ArticleDetail';

ArticleDetail.propTypes = {};
ArticleDetail.defaultProps = {};

export default ArticleDetail;



